import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { getKnowledges, deleteKnowledge } from 'services/knowledgeUnit';

class UnitKnowledgePage extends Component {
  state = {
    reload: 0,
  };

  handleDelete = async id => {
    await deleteKnowledge(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'Tên kiến thức',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        align: 'center',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#unit-knowledge/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        )
      },
    ];
    return (
      <Card title="Danh mục khối kiến thức">
        <CartHeadAction>
          <BtnAddNew href="#/unit-knowledge/form/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getKnowledges}
              columns={columns}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <UnitKnowledgePage />
  </Page>
);



